.movieContainer {
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.movieSummary {
  display: flex;
  gap: 4px;
}

.coverImage {
  border-radius: 10%;
}
.genresLiContainer {
  border: 2px solid red;
  outline: none;
  display: flex;
  flex-direction: column-reverse;
  justify-content: center;
  align-items: center;
}

.genresLi {
  list-style: none;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 6px;
  border: 1px solid black;
  margin: 0;
  top: 0;
}

.title {
  font-family: cursive;
}

.banner {
  width: 40%;
  height: 40%;
  border-radius: 10%;
}

.container {
  background-color: #181818;
  width: 100vw;
  height: 100vh;
}
